/*基础http通用接口*/
define([], function() {
    //var baseUrl = "http://www.nuannuanzhibo.com/";
    var baseUrl = "http://www.mengquww.com";
    var downloadUrl = "http://www.mengquww.com/download/gw";

    var getBaseUrl = function() {　　　　
        var url = location.href;
        if (url.indexOf("mengquww.com") != -1) {
            baseUrl = "http://www.mengquww.com";
        }else  {
            baseUrl = "http://122.11.48.251:6639/DollAppServer";
        }
        return baseUrl;
    };


    var getDownloadUrl = function() {　　　　
        return downloadUrl;
    };

    var request = function(paramName) {
        var url = location.href;
        var paraString = url.substring(url.indexOf("?") + 1, url.length).split(
            "&");
        var params = {}
        for (i = 0; j = paraString[i]; i++) {
            params[j.substring(0, j.indexOf("=")).toLowerCase()] = j.substring(
                j.indexOf("=") + 1, j.length);
        }
        var returnValue = params[paramName.toLowerCase()];
        if (typeof(returnValue) == "undefined") {
            return "";
        } else {
            return returnValue;
        }
    };

    //读取cookies 
    var getCookie = function(name) {
        var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
        //alert(arr,reg);
        if (arr = document.cookie.match(reg))

            return unescape(arr[2]);

        else
            return null;
    };

    function setCookie(name, value) {
        var Days = 30;
        var exp = new Date();
        exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
        document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString();
    }


    var isWeixinVisit = function() {
        var ua = navigator.userAgent;
        var isWeixin = /MicroMessenger/i.test(ua);
        return isWeixin;
    }
    var isMobile = function() {
        var sUserAgent = navigator.userAgent.toLowerCase();
        var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
        var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
        var bIsMidp = sUserAgent.match(/midp/i) == "midp";
        var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
        var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
        var bIsAndroid = sUserAgent.match(/android/i) == "android";
        var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
        var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
        if (bIsIpad || bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM) {
            return true;
        } else {
            return false;
        }
    }
    var isShareVisit = function() {
        var shareFlag = request('shareFlag');
        if (shareFlag == 1) {
            return true;
        }
        return false;
    }

    var jumpDownloadUrl = function() {
        window.location.href = downloadUrl;
    }
    return {
        getBaseUrl: getBaseUrl,
        request: request,
        getCookie: getCookie,
        setCookie: setCookie,
        isWeixinVisit: isWeixinVisit,
        isShareVisit: isShareVisit,
        getDownloadUrl: getDownloadUrl,
        jumpDownloadUrl: jumpDownloadUrl,
        isMobile:isMobile,
    };
});


