<template>
  <section class="maxBox">
    <img src="http://lianai-image-sys.qiniudn.com/wa/s_bg.png" alt="" class="s_bg">
    <div class="shareCen">
      <div class="shareCen_In">
        <div class="sharePic">
          <span></span>
          <img :src="info.img" alt="">
          <!-- 公仔图 -->
          <p v-text="info.name"></p>
        </div>
        <h6 class="yq" v-if="info.code>0">我的邀请码：<em v-text="info.code"></em></h6>
        <!-- 邀请码 -->
      </div>
    </div>
    <div class="footer s_footer"><img src="http://lianai-image-sys.qiniudn.com/wa/footer.png" @click="jumpDownload" alt=""></div>
  </section>
</template>
<script>
import user from './assets/js/user.js'
import doll from './api/doll/doll'
import common from './common/common.js'
import httpUtils from './common/httpUtils.js'

export default {
  name: 'app',
  components: {},
  data() {
    return {
      uid: 1832306,
      did:1,
      info: {},
    }
  },
  filters: {
    fmtDate: function(value) {
      if (!value) return ''
      return common.fmtDate(value);
    }
  },
  created: function() {
    this.uid = httpUtils.request("uid");
    this.did = httpUtils.request("did");
    console.log(this.uid);
    let self = this;
    doll.getWin(this.uid,this.did).then(function(res) {
      console.log(res);
      self.info = res.data;
    }).catch(function(err) {
      console.log(err);
    });
  },
  methods: {
    jumpDownload: function() {
      httpUtils.jumpDownloadUrl();
    }

  }
}

</script>
<style>


</style>



